import { initAos, initAosScale, initStaggeredAos } from './plugins/aos';
import { initLenis } from './plugins/lenis';
import { getLenis } from './plugins/lenis';
import { initTrMarquee } from './plugins/scroll-marquee';
import { createSplitText, initFadeEatchWordOnScroll, splitLines } from './plugins/split-text';

CustomEase.create('primary-ease', '0.62, 0.05, 0.01, 0.99');
CustomEase.create('primary-ease-out', '0.17,0.84,0.44,1');
CustomEase.create('smooth', '.15, .9, .34, .95');
CustomEase.create('custom', '.76, 0, .24, 1');

ScrollTrigger.defaults({
  markers: false,
  scroller: document.documentElement,
});

const isMobile = window.matchMedia('(max-width: 768px)').matches;

function initTabsPagination() {
  console.log('🚀 initTabsPagination() started');

  const panes = document.querySelectorAll('.w-tab-pane');

  if (!panes.length) {
    console.warn('❌ No .w-tab-pane found');
    return;
  }

  console.log(`📦 Found ${panes.length} tab panes`);

  panes.forEach((pane, paneIndex) => {
    console.group(`🟦 TAB ${paneIndex + 1} — ID: ${pane.id || 'no-id'}`);

    const items = Array.from(pane.querySelectorAll('.portfolio-item'));
    console.log('🔢 Items found:', items.length);

    const pagination = pane.querySelector('.portfolio-pagination');
    if (!pagination) {
      console.warn('❌ No .portfolio-pagination found');
      console.groupEnd();
      return;
    }

    const list = pagination.querySelector('.pagination-list');
    const prevBtn = pagination.querySelector('.pagination-nav.is-prev');
    const nextBtn = pagination.querySelector('.pagination-nav.is-next');

    const perPage = parseInt(pane.dataset.perPage) || 6;
    const totalPages = Math.ceil(items.length / perPage);

    console.log('📄 perPage:', perPage);
    console.log('📊 totalPages:', totalPages);

    if (totalPages <= 1) {
      console.warn('⚠ Only 1 page or less. Pagination not needed.');
    }

    let currentPage = 1;

    function formatNumber(num) {
      return num;
    }

    function generatePages(current, total, delta = 2) {
      console.log(`🧠 Generating pages: current=${current}, total=${total}`);

      const range = [];
      const result = [];
      let last;

      for (let i = 1; i <= total; i++) {
        if (i === 1 || i === total || (i >= current - delta && i <= current + delta)) {
          range.push(i);
        }
      }

      for (const i of range) {
        if (last) {
          if (i - last === 2) {
            result.push(last + 1);
          } else if (i - last > 2) {
            result.push('dot');
          }
        }
        result.push(i);
        last = i;
      }

      console.log('📌 Pages generated:', result);
      return result;
    }

    function showPage(page) {
      console.group(`➡ showPage(${page})`);
      currentPage = page;

      const start = (page - 1) * perPage;
      const end = start + perPage;

      console.log('🟢 Showing index range:', start, 'to', end - 1);

      items.forEach((item, index) => {
        const visible = index >= start && index < end;
        item.style.display = visible ? '' : 'none';
      });

      renderPagination();

      // 🔥 IMPORTANT: tunggu layout settle
      setTimeout(() => {
        console.log('♻ Updating Lenis + ScrollTrigger');

        if (window.lenis) {
          window.lenis.resize(); // update internal height
          window.lenis.scrollTo(window.scrollY); // sync position
        }

        ScrollTrigger.refresh(true);

        console.log('✅ ScrollTrigger refreshed');
      }, 100);

      console.groupEnd();
    }

    function createBullet(page) {
      const button = document.createElement('button');
      button.classList.add('pagination-bullet');

      const textWrap = document.createElement('div');
      textWrap.classList.add('pagination-text-wrap');

      const text = document.createElement('div');
      text.classList.add('text-14');

      if (page === 'dot') {
        button.classList.add('is-dot');
        button.disabled = true;
        text.textContent = '...';
      } else {
        text.textContent = formatNumber(page);

        if (page === currentPage) {
          button.classList.add('is-active');
        }

        button.addEventListener('click', () => {
          console.log(`🖱 Bullet clicked: ${page}`);
          showPage(page);
        });
      }

      textWrap.appendChild(text);
      button.appendChild(textWrap);

      return button;
    }

    function renderPagination() {
      console.group('🎨 renderPagination()');

      if (!list) {
        console.warn('❌ No .pagination-list found');
        console.groupEnd();
        return;
      }

      list.innerHTML = '';

      const pages = generatePages(currentPage, totalPages);

      pages.forEach((page) => {
        const bullet = createBullet(page);
        list.appendChild(bullet);
      });

      prevBtn?.classList.toggle('is-disabled', currentPage === 1);

      nextBtn?.classList.toggle('is-disabled', currentPage === totalPages);

      console.log('✅ Pagination rendered');
      console.groupEnd();
    }

    prevBtn?.addEventListener('click', () => {
      console.log('⬅ Prev clicked');
      if (currentPage > 1) {
        showPage(currentPage - 1);
      } else {
        console.warn('⚠ Already at first page');
      }
    });

    nextBtn?.addEventListener('click', () => {
      console.log('➡ Next clicked');
      if (currentPage < totalPages) {
        showPage(currentPage + 1);
      } else {
        console.warn('⚠ Already at last page');
      }
    });

    const tabLink = document.querySelector(`.w-tab-link[href="#${pane.id}"]`);

    tabLink?.addEventListener('click', () => {
      console.log('🔁 Tab changed → reset to page 1');
      currentPage = 1;
      showPage(1);
    });

    showPage(1);

    console.groupEnd();
  });

  console.log('✅ initTabsPagination() completed');
}

function loadAnimation() {
  const hero = document.querySelector('.hero.is-home');
  if (!hero) return;

  const nav = document.querySelector('.navbar');

  const bg = hero.querySelector('.bg-hero');
  const title = hero.querySelector('.hero-head');
  const text = hero.querySelector('.text-20');
  const running = hero.querySelector('.hero-running');
  const btn = hero.querySelector('.btn');

  const splittedTitle = new SplitText(title, { types: 'lines, words', mask: 'lines' });
  const splittedText = new SplitText(text, { types: 'lines, words', mask: 'lines' });

  gsap.set([running, btn], { autoAlpha: 0, y: '3rem' });
  gsap.set([...splittedTitle.lines, ...splittedText.lines], { yPercent: 100 });
  gsap.set(bg, { scale: 1.3, autoAlpha: 1, x: '0' });
  gsap.set(nav, { autoAlpha: 0 });

  const tl = gsap.timeline({ defaults: { ease: 'primary-ease' } });

  tl.to(bg, { scale: 1, x: '0', autoAlpha: 1, duration: 1.2 }, 0);
  tl.to(nav, { autoAlpha: 1, duration: 1.2 })
    .to(splittedTitle.lines, { yPercent: 0, duration: 1.2 }, '<=0')
    .to(splittedText.lines, { yPercent: 0, duration: 1.2 }, '<=0')
    .to(running, { autoAlpha: 1, y: 0, duration: 1.2 }, '<=0')
    .to(btn, { autoAlpha: 1, y: 0, duration: 1.2 }, '<=0');
}

function initApp() {
  const mm = gsap.matchMedia();

  initLenis();
  window.lenis = getLenis();
  //reusable functions
  splitLines();
  initFadeEatchWordOnScroll();
  createSplitText();
  initAos();
  initAosScale();
  initStaggeredAos();
  initTrMarquee();

  loadAnimation();

  if (isMobile) {
    // mobile only
    initTabsPagination();
  }
}

if (document.readyState === 'complete') {
  initApp();
} else {
  window.addEventListener('load', initApp);
}

window.onload = function () {
  window.scrollTo({
    top: 0,
    left: 0,
    behavior: 'smooth', // For smooth animation
  });

  // ScrollTrigger.refresh();
};

document.addEventListener('resize', () => {
  ScrollTrigger.refresh();
});
